<script>
    import { createEventDispatcher, onMount } from 'svelte';
    
    import Card from './Card.svelte';
    import FaRegCircle from 'svelte-icons/fa/FaRegCircle.svelte'
    import FaWindowClose from 'svelte-icons/fa/FaWindowClose.svelte'
    import FaCheckCircle from 'svelte-icons/fa/FaCheckCircle.svelte'
    import { playLocalAudio } from './utility/AudioUtility';
    import { shuffle } from './utility/CollectionUtility';

    export let phrasesObject;
    export let holdFlip;
    export let isMobile;

    let hasChosen;

    const newQuestionDispatcher = createEventDispatcher();

    function checkAnswer(answer){
        hasChosen = true;
        if(isCorrect(answer))
        {
            playLocalAudio("good-answer.wav");
            newQuestionDispatcher('newQuestion', { isCorrect: true, chosenAnswer: answer, question: phrasesObject.frontPhrase, correctAnswer: phrasesObject.correctPhrase });
        }
        else
        {
            playLocalAudio("bad-answer.wav");
            newQuestionDispatcher('newQuestion', { isCorrect: false, chosenAnswer: answer, question: phrasesObject.frontPhrase, correctAnswer: phrasesObject.correctPhrase });
        }

    }
    function isCorrect(answer){
        return answer === phrasesObject.correctPhrase;
    }

    onMount(() =>{
        phrasesObject.Answers = shuffle(phrasesObject.Answers)
    });
</script>
<style>
    h3 {
        overflow-wrap: break-word;
        margin: 3px;
    }

    h3{
        line-height: 1.2;
        text-align: center;
    }
    .icon{
        color: #5a4ba5;
        margin-right: 5px;
    }
    .icon-answer
    {
        height: 24px;
        width: 24px;
        margin-right: 12px;
        position: relative;
        top: 6px;
    }
    .icon-mobile-answer{
        height: 12px;
        width: 12px;
    }
    .answer-option{
        font-size: 18px;
    }
    .answer-option-mobile{
        font-size: 12px;
    }
    .lx-collection-item{
        border-radius: 5px;
        background-color: #3b3f54;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .is-correct{
        background-color: #5cb85c!important;
    }
    .is-correct .icon{
        color: #0e9b0e;
    }
    .is-incorrect{
        background-color: #e87874!important;
    }
    .is-incorrect .icon{
        color: #d9534f;
    }
    .front-phrase-mobile{
        /* margin-top: 40px; */
        position: relative;
        top: 15px;
        font-size: 18px;
    }
</style>

<Card holdFlip={holdFlip} isMobile={isMobile}>
    <div slot="front">
        <div class="lx-row">
            <div class="lx-column" class:front-phrase-mobile={isMobile}>
                {#if isMobile}
                    <span>{phrasesObject.frontPhrase}</span>
                {:else}
                <h3>{phrasesObject.frontPhrase}</h3>
                {/if}
                
            </div>
            <div class="lx-column">
                <ul class="lx-collection">
                    {#each phrasesObject.Answers as answer}
                        <li class="lx-collection-item" 
                        class:is-correct={hasChosen && isCorrect(answer)}
                        class:is-incorrect={hasChosen && !isCorrect(answer)}
                        on:click={checkAnswer(answer)}>
                            <span class="icon" class:icon-answer={!isMobile} class:icon-mobile-answer={isMobile}>
                                
                                {#if hasChosen && isCorrect(answer)}
                                    <FaCheckCircle/>
                                {:else if hasChosen && !isCorrect(answer)}
                                    <FaWindowClose/>
                                {:else}
                                    <FaRegCircle/>
                                {/if}
                                
                            </span>
                            <span class:answer-option={!isMobile}
                            class:answer-option-mobile={isMobile}>{answer}</span>
                        </li>
                    {/each}
                </ul>
            </div>
        </div>
    </div>
    <div slot="back">
    </div>
</Card>