<script>
    import { tweened } from 'svelte/motion';
	import { cubicOut } from 'svelte/easing';

    function setPercentage(percentage){
        progress.set(percentage);
    }

	export const progress = tweened(0, {
		duration: 400,
		easing: cubicOut
	});
</script>

<style>
    progress {
        width: 90%;
    }
</style>

<progress value={$progress}></progress>
