<script>
    import MdAudiotrack from 'svelte-icons/md/MdAudiotrack.svelte'
    import Card from './Card.svelte';
    import { playGDriveAudio  } from './utility/AudioUtility'

    export let phrasesObject;
    export let holdFlip;
    export let isMobile;

</script>
<style>
    .audio-icon{
        width: 130px;
        text-align: center;
    }
    .front-side, .back-side{
        display: flex;
        align-items:center;
        justify-content:center;
    }

</style>

<Card holdFlip={holdFlip} isMobile={isMobile}>
    <div slot="front" class="front-side">
        <div class="audio-icon" on:click|stopPropagation={playGDriveAudio(phrasesObject.audioPhraseFront)}>
            <MdAudiotrack/>
            <span>(Click to play)</span>
        </div>
    </div>
    <div slot="back" class="back-side">
        <div class="audio-icon" on:click|stopPropagation={playGDriveAudio(phrasesObject.audioPhraseBack)}>
            <MdAudiotrack/>
            <span>(Click to play answer.)</span>
        </div>
        <div>
            {#if isMobile}
                <p>Question: {phrasesObject.frontPhrase}</p>
                <p>Answer: {phrasesObject.backPhrase}</p>
            {:else}
                <h3>Question: {phrasesObject.frontPhrase}</h3>
                <h3>Answer: {phrasesObject.backPhrase}</h3>
            {/if}
        </div>
    </div>
</Card>